.page-header {
  background-color: #ffffff;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 126px;
    width: 100%;
    height: 1px;
    background-color: #f6f5f4;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #f6f5f4;
  }

  & input[type="search"] {
    width: 180px;
    border: none;
    outline-color: #f6f5f4;
    box-sizing: border-box;
    padding: 0;
    padding-left: 11px;
  }

  & button {
    background-color: transparent;
    border: none;
    outline: none;
    padding: 0;
  }
}

.page-header input[type="search"]:hover {
  border-bottom: 1px solid #9b9997;
}

.page-header input[type="search"]:focus {
  border-bottom: 1px solid #9b9997;
}

.page-header input[type="search"]::placeholder {
  font-size: 14px;
}

.page-header button:hover {
  opacity: 0.6;
}

.page-header button:focus {
  opacity: 0.3;
}

.page-header__head {
  max-width: 1366px;
  margin: 0 auto;
  padding: 41px 98px 10px;
  padding-right: 88px;
  box-sizing: border-box;

  display: flex;
  align-items: flex-start;

  & form {
    padding-top: 10px;
  }
}

.page-header__logo {
  margin-left: 30.2%;
  margin-right: auto;
  cursor: auto;
}

.page-header__logo--inner {
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.3;
  }
}

.page-header__login {
  width: 38px;
  margin-right: 33px;
  color: #1d1613;
  margin-top: 12px;

  text-decoration: none;

  font-weight: 500;
  font-size: 14px;
  line-height: 21px;

  &:hover {
    color: #9b9997;
  }

  &:focus {
    opacity: 0.7;
  }
}

.page-header__cart {
  width: 49px;
  color: #1d1613;
  margin-top: 12px;
  margin-right: 10px;
  text-align: right;
  letter-spacing: 0.7px;

  text-decoration: none;

  font-weight: 500;
  font-size: 14px;
  line-height: 21px;

  &:hover {
    color: #9b9997;
  }

  &:focus {
    opacity: 0.7;
  }
}

.page-header__cart svg {
  display: none;
}

@media (max-width: 1023px) {
  .page-header {
    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
    position: relative;
  }

  .page-header__head {
    padding: 30px 45px;
    padding-bottom: 25px;
    max-width: 100%;

    & form {
      display: none;
    }
  }

  .page-header--colored {
    background-color: #a87b62;
  }

  .page-header--colored .page-header__toggle {
    background-image: url("../img/toggle-white.svg");
  }

  .page-header--colored .page-header__logo svg {
    stroke: #ffffff;
  }

  .page-header--colored .page-header__cart svg {
    stroke: #ffffff;
  }


  .page-header__toggle {
    cursor: pointer;
    display: block;
    width: 25px;
    height: 16px;
    background-image: url("../img/toggle.svg");
    background-position: center;
    background-repeat: no-repeat;
    margin-top: 25px;
  }

  .page-header__logo {
    margin: 0;
    margin-right: auto;
    margin-left: 41.5%;
  }

  .page-header__login {
    display: none;
  }

  .page-header__cart {
    width: auto;
    margin-top: 25px;

    & span {
      display: none;
    }

    & svg {
      display: block;
      fill: transparent;
      stroke: #1d1613;
    }
  }
}

@media (max-width: 767px) {
  .page-header__head {
    padding: 20px 15px;
    padding-bottom: 14px;
    max-width: 100%;
  }

  .page-header__toggle {
    margin-top: 18px;
  }

  .page-header__logo {
    margin: 0 auto;
    transform: translate(-6%, 0);

    & svg {
      width: 52px;
      height: 52px;
    }
  }

  .page-header__logo--inner svg {
    width: 52px;
    height: 52px;
  }

  .page-header__cart {
    margin-top: 17px;
  }
}
